@use '../helpers' as *;
@use '../components' as *;
@use 'variables' as *;
@import '../helpers/functions';

//@import 'gerillass';

/**
 * Main styles
 */
@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;700;900&display=swap');


html {
    font-size: 100%;
}


*,
::before,
::after {
    box-sizing: border-box;
}

[hidden],
[hidden] * {
    display: none !important;
}

body {
    min-height: 100vh;
    margin: 0;
    @include font-family('Inter', 1rem);
    color: $main-color;
    background-color: $main-bg-color;

}


a {
    text-decoration: none;
}

h1 {
    @include font-size(3.75rem, 3.75rem, 900);
    color: $main-heading-color;
    @include padding-y(.5rem);
    margin: 0 2rem 2rem 0;
}


p {
    @include font-size(15px, 25px);
    padding: 1rem 0;

    margin: {
        top: 0;
        bottom: 2rem;
    }
}


.container {
    @extend %no-overflow;
    width: 90vw;
    max-width: 1200px;
    @include margin-x(auto);
    padding: 0;
    display: flex;

    &+.container {
        margin-top: 1rem;
    }
}


header {
    display: flex;
    height: 60px;

    margin: {
        top: 2.5rem;
        bottom: 42px;
    }

    color: $header-color;
    background: $header-bg-color;
    user-select: none;


    a {
        color: $header-color;

        &:hover,
        &:active {
            color: $header-hover-color;
        }
    }

    nav {
        display: flex;
        text-align: center;
        @include font-size(17px, 21px);

        justify-items: center;
        align-items: flex-start;

        a {
            padding: .75rem 0;
            margin-right: 2rem;
            position: relative;

            &:last-child {
                margin-right: 0;
            }

            &:before {
                position: absolute;
                content: '';
                bottom: .25rem;
                left: 50%;
                right: 50%;
                height: 3px;
                background: $header-color;
                transition: .75s;

            }

            &:hover,
            &:active {
                font-weight: 700;
                text-shadow: $box-shadow;

                &:before {
                    left: 25%;
                    right: 25%;
                }
            }

        }

        @include md-down {
            display: none;
        }

    }

    .logo {
        margin-right: auto;
        height: __remify(44px);
        line-height: __remify(44px);
        overflow: hidden;
    }

    .nav-btn {
        @include sizer(40px);

        @include md {
            display: none;
        }
    }


}




.btn {
    @include button(17px, 25px);

    &.btn-pink {
        border: $pink-btn-border;
        color: $pink-btn-color;
        background-color: $pink-btn-bg-color;

        &:hover,
        &:active {
            color: $pink-btn-hover-color;
            background: $pink-btn-hover-bg-color;
            border: $pink-btn-hover-border;
        }
    }
}

.card {

    display: flex;
    flex-direction: column;

    @extend %no-overflow;




    .card-header {
        @include font-size(1.9375rem, 1.5625rem, 900);
        color: $card-heading-color;
        margin-bottom: .75rem;
    }

    .card-title {
        @include font-size(1.1875rem, 1.5625rem, 900);
        text-transform: capitalize;
        color: $card-title-color;

        &:hover,
        &:active {
            color: $card-title-hover-color;
        }
    }
}

.main-article {

    .picture {
        @extend %no-overflow;
        width: 100%;
        max-width: 793px;

        img {
            width: 100%;
            height: 20.25rem;

            object: {
                position: center;
                fit: cover;
            }
        }
    }

    .article {

        margin: 2rem 0;
        max-width: 793px;
        color: $main-content-color;
        padding-bottom: 2rem;

        .heading {
            color: $main-heading-color;
        }


    }
}

#main {

    display: flex;
    flex-direction: column;
    flex-wrap: nowrap;

    .main-article {
        display: flex;
        flex-direction: column;

    }

    .news {
        flex-direction: column;
        flex-shrink: 1 0 auto;
    }

    @include md {
        flex-direction: row;

        .main-article {
            padding-right: 1rem;
        }
    }

}

.news {
    padding: 2rem;
    background-color: $featured-news-bg-color;
    color: $featured-news-color;

    .news-heading {
        @include font-size(__remify(40px), __remify(60px), 900);
        color: $featured-news-main-heading-color;
    }

    .card {
        padding-top: 2rem;

        // padding: 1rem 0;
        .card-title {
            color: $featured-news-title-color;
            text-transform: capitalize;

            &:hover,
            &:active {
                color: $featured-news-title-hover-color;
            }
        }

        .card-header {
            @include font-size(__remify(19px), __remify(25px), 900);
            color: $featured-news-title-color;
            text-transform: capitalize;

            &:hover,
            &:active {
                color: $featured-news-title-hover-color;
            }
        }

        &:not(:last-of-type) {
            border-bottom: 1px solid $featured-news-separator-color;
        }

    }
}


.related {


    flex-direction: column;


    .card {

        flex-direction: row;
        height: 139px;

        @include margin-y(1.5rem);

        .card-img {
            img {
                object: {
                    position: center;
                    fit: cover;
                }

                @include sizer(109px, 139px);
            }

        }

        .card-body {
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            @include padding-x(1.43rem);

            &>* {
                padding: 0;
                margin: 0;
            }
        }



    }

    @include lg {
        flex-direction: row;
    }
}


@include generate-grid;